<template>
  <div class="tab-zone">
    <p
      class="tab-item tab-text"
      @click="clickBarItem(0)"
      :class="currentIndex === 0 ? 'selected-item' : ' '"
    >
      {{ list[0] }}
    </p>

    <p
      class="tab-item tab-text"
      @click="clickBarItem(1)"
      :class="currentIndex === 1 ? 'selected-item' : ' '"
    >
      {{ list[1] }}
    </p>
    <p
      class="tab-item tab-text"
      @click="clickBarItem(2)"
      :class="currentIndex === 2 ? 'selected-item' : ' '"
    >
      {{ list[2] }}
    </p>

    <p
      class="tab-item tab-text"
      @click="clickBarItem(3)"
      :class="currentIndex === 3 ? 'selected-item' : ' '"
    >
      {{ list[3] }}
    </p>

    <p
      class="tab-item tab-text"
      @click="clickBarItem(4)"
      :class="currentIndex === 4 ? 'selected-item' : ' '"
    >
      {{ list[4] }}
    </p>

    <p
      class="tab-item tab-text"
      @click="clickBarItem(5)"
      :class="currentIndex === 5 ? 'selected-item' : ' '"
    >
      {{ list[5] }}
    </p>

    <p
      class="tab-item tab-text"
      @click="clickBarItem(6)"
      :class="currentIndex === 6 ? 'selected-item' : ' '"
    >
      {{ list[6] }}
    </p>

    <p
      class="tab-item tab-text"
      @click="clickBarItem(7)"
      :class="currentIndex === 7 ? 'selected-item' : ' '"
    >
      {{ list[7] }}
    </p>
    <p class="tab-item tab-text" @click="reset">返回</p>
  </div>
</template>

<script>
export default {
  name: "e-TabBar",
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      currentIndex: 0,
    };
  },
  methods: {
    reset() {
      this.$emit("resetprod");
    },
    clickBarItem(index) {
      this.currentIndex = index;
      let code;
      switch (index) {
        case 0:
          code = 1100;
          break;
        case 1:
          code = 1101;
          break;
        case 2:
          code = 1102;
          break;
        case 3:
          code = 1103;
          break;
        case 4:
          code = 1104;
          break;
        case 5:
          code = 1105;
          break;
        case 6:
          code = 1106;
          break;
        case 7:
          code = 1107;
          break;
        default:
          code = 1100;
      }
      this.$emit("tabBarItemClick", code);
    },
  },
};
</script>

<style scoped>
.tab-zone {
  display: flex;
  justify-content: space-around;
  align-items: center;

  width: 70%;
  margin-bottom: 30px;

  font-size: 17px;
  color: #000;
}
.tab-zone .tab-item {
  height: 30px;
  margin-right: 20px;
}
.tab-zone .tab-item.tab-text {
  cursor: pointer;

  text-overflow: ellipsis;
  white-space: nowrap;
}
.tab-zone .tab-item.as-line {
  width: 0;
  border-right: 1px solid #000;
}
.selected-item {
  width: 80px;
  padding-bottom: 40px;
  border-bottom: 3px solid #95d602;

  text-align: center;
  font-weight: bold;
  color: #000;
}
</style>
