<template>
  <div id="order-management">
    <div class="order-list" v-show="showorder">
      <tab-bar
        :list="orderStatusList"
        @tabBarItemClick="changeActiveList"
        @resetprod="reset"
      ></tab-bar>
      <scroll class="scroll-content" ref="scroll">
        <content-list
          :list="activeShowList"
          class="contentList"
          @changeControln="changeControln"
          @checkFormClick="showPrepareForm"
          @confirmOrder="confirmOrder"
          @completeOrder="completeOrder"
          v-if="activeShowList.length !== 0"
        ></content-list>

        <div class="paginationZone">
          <a-pagination
            :total="activeList ? activeList.length : 0"
            :show-total="
              (total, range) => `${range[0]}-${range[1]} of ${total} items`
            "
            :page-size="pageSize"
            :default-current="1"
            v-model:current="currentPage"
            v-if="activeShowList.length !== 0"
          ></a-pagination>
        </div>

        <empty-data-placeholder
          v-if="activeShowList.length === 0"
          description="暂无订单"
        ></empty-data-placeholder>
      </scroll>

      <div
        class="right-cover"
        v-show="ifShowPrepareForm"
        :class="
          fadeInAnimate
            ? 'FriendUrlCreateWindowFadeIn'
            : 'FriendUrlCreateWindowFadeOut'
        "
      >
        <prepare-form
          ref="prepareForm"
          @closeIconClick="closePrepareForm"
          :formData="getFormData()"
        ></prepare-form>
      </div>

      <div class="fixedFullLoading" v-show="isLoading">
        <a-space>
          <a-spin size="large"></a-spin>
        </a-space>
      </div>
    </div>
    <div class="product-list" v-show="!showorder">
      <product-list :prods="prodlist" @setprod="setprod"> </product-list>
    </div>
  </div>
</template>

<script>
import TabBar from "./childComponents/tabBar/TabBar.vue";
import ProductList from "./childComponents/productList/ProductList.vue";
import Scroll from "../../../components/common/scroll/Scroll.vue";
import ContentList from "./childComponents/contentList/ContentList.vue";
import PrepareForm from "./childComponents/prepareForm/PrepareForm.vue";
import EmptyDataPlaceholder from "../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue";
import {
  $getAllOrderByProviderID,
  $confirmOrder,
  $completeOrder,
} from "../../../network/providerTools/order";
import { $getActivityInfoById } from "../../../network/providerTools/activity";

export default {
  name: "e-orderManagement",
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isProvider") === "true") {
      window.localStorage.setItem("currentIndex010006", 3);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010006");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  activated() {},
  async created() {
    this.isLoading = true;
    let obtainedData;
    // 获取当前登录provider的全部订单存放到this.$data.all中
    const requestPayload = {
      id: Number(window.localStorage.getItem("providerID")),
    };
    try {
      obtainedData = await $getAllOrderByProviderID(requestPayload);
      console.log("all list ; ", obtainedData);
      if (obtainedData.Code === 200 && obtainedData.msg === "success") {
        console.log("succes");
        if (obtainedData.details.orders.length === 0) {
          this.$toast.success("您还没有订单数据", 240);
        } else {
          this.$toast.success("订单数据获取成功", 240);
        }
        let orders = obtainedData.details.orders;
        // console.log('succes')

        // console.log(obtainedData.details.orders[1])

        //假数据
        // console.log(orders,"riginal")
        for (let i = 0; i < orders.length; i++) {
          orders[i].thumbnail =
            "/media/%E6%A8%AA%E6%9D%BF%E9%A3%8E%E9%87%871_mK0Bs09.jpg";
          orders[i].topic = "热爱AI的青少年入门体验营";
          orders[i].duration = "03/04-03/03";
        }
        console.log(orders, "ooooriginal");
        let orderdupar = [orders];
        let orderdup = [...orders];
        console.log(orderdupar);
        console.log(orderdup);
        for (let i = 1; i < 5; i++) {
          orderdupar[i] = orders.map((e) => {
            // console.log('e:',e)
            let oo = { ...e };
            console.log(e);
            // console.log('map e: ', e)
            oo.product = 10 + i;
            oo.prodstatus = i;
            oo.thumbnail =
              "/media/%E6%A8%AA%E6%9D%BF%E9%A3%8E%E9%87%871_mK0Bs09.jpg";
            oo.topic = "热爱AI的青少年入门体验营";
            oo.duration = "03/04-03/03";
            // console.log('oo: ', oo)
            // console.log('ooL ',oo)
            console.log(oo);
            return oo;
          });
          console.log(orderdupar);
          // console.log('orderdupar[i]',orderdupar[i])
          orderdup = [...orderdup, ...orderdupar[i]];
          //  console.log('orderdupup',newdup)
        }
        console.log("after th loop orders:");
        console.log(orderdup);
        //假数据

        // 将orders 按产品id 分组
        // 按product Id排序
        let prodlist = orderdup;
        prodlist.sort((a, b) => a.product - b.product);
        let sortedprod = prodlist;

        // 二维数组 每个数组是同一个product的订单列表
        let ar2 = [[]];
        ar2[0][0] = sortedprod[0];

        //  遍历整理
        for (let i = 1; i < sortedprod.length; i++) {
          let length1 = ar2.length;
          let length2 = ar2[length1 - 1].length;
          // console.log('length1: ',length1,' length2: ',length2,' last order: ',ar2[length1-1][length2-1],';new order: ',sortedprod[i])
          if (ar2[length1 - 1][length2 - 1].product == sortedprod[i].product) {
            ar2[length1 - 1][length2] = sortedprod[i];
          } else {
            ar2[length1] = [];
            ar2[length1][0] = sortedprod[i];
          }
        }
        console.log("prod ar: ", ar2);

        this.prodlist = ar2;
        console.log(this.prodlist);
        this.allList = orders;
      } else {
        this.$toast.error("订单数据获取失败", 240);
        return;
      }
    } catch (err) {
      console.log(err);
      this.$toast.error("订单数据获取失败", 240);
      return;
    }

    // 造数据
    /* for(let i = 10; i < 20; i++) {
      this.$set(this.allList[i], 'status', 1102);
    }
    for(let i = 20; i < 30; i++) {
      this.$set(this.allList[i], 'status', 1103);
    }
    for(let i = 30; i < 40; i++) {
      this.$set(this.allList[i], 'status', 1104);
    }
    for(let i = 40; i < 50; i++) {
      this.$set(this.allList[i], 'status', 1105);
    }
    for(let i = 50; i < 60; i++) {
      this.$set(this.allList[i], 'status', 1106);
    }
    for(let i = 60; i < 70; i++) {
      this.$set(this.allList[i], 'status', 1107);
    }
    for(let i = 70; i < obtainedData.length; i++) {
      this.$set(this.allList[i], 'status', 1108);
    } */

    // allList准备好了
  },
  data() {
    return {
      showorder: false,
      orderStatusList: [
        "全部",
        "已下单",
        "已付款",
        "待参加",
        "已参加",
        "申请退款",
        "已退款",
        "取消订单",
      ],
      allList: [],
      prodlist: [],
      activeList: [],
      activeShowList: [],

      isLoading: false,
      pageSize: 8,
      currentPage: 1,

      labelCode: 1100,

      ifShowPrepareForm: false,
      fadeInAnimate: null,
      indexOfActiveShowList: -1,
    };
  },
  methods: {
    reset() {
      this.showorder = false;
    },
    async setprod(ee) {
      console.log("set prod in order manage  :", ee);
      let index;
      this.prodlist.forEach((e) => {
        if (e[0].product == ee) {
          index = this.prodlist.indexOf(e);
        }
      });
      console.log(index);
      this.showorder = true;
      this.allList = this.prodlist[index];

      this.activeList = this.allList; // 指向同一块栈空间
      this.activeShowList = this.activeList.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );

      this.isLoading = false;

      for (let i = 0; i < this.allList.length; i++) {
        const requestPayload = {
          id: this.allList[i].product,
        };
        const response = await $getActivityInfoById(requestPayload);
        // console.log(response.details)
        if (response.Code === 200 && response.msg === "success") {
          if (response.details.horizontal_shows.length !== 0) {
            this.$set(
              this.allList[i],
              "thumbnail",
              response.details.horizontal_shows[0].url
            );
          }
          this.$set(this.allList[i], "topic", response.details.topic);
          // 处理时间
          const startTimeArr = response.details.activity_start_time.split("-");
          const startDateArr = startTimeArr[2].split("T");
          const processedStartTime = `${startTimeArr[1]}/${startDateArr[0]}`;

          const endTimeArr = response.details.activity_end_time.split("-");
          const endDateArr = endTimeArr[2].split("T");
          const processedEndTime = `${endTimeArr[1]}/${endDateArr[0]}`;
          const duration = `${processedStartTime}-${processedEndTime}`;
          this.$set(this.allList[i], "duration", duration);

          this.$set(this.allList[i], "city", response.details.location_city);
          this.$set(this.allList[i], "controln", 0);
        }
      }
    },
    changeActiveList(code) {
      this.labelCode = code;
    },
    resetPage() {
      this.currentPage = 1;
    },
    changeControln(index) {
      if (this.activeShowList[index].controln === 0) {
        this.$set(this.activeShowList[index], "controln", 1);
      } else {
        this.$set(this.activeShowList[index], "controln", 0);
      }
    },
    showPrepareForm(index) {
      this.indexOfActiveShowList = index;
      this.ifShowPrepareForm = true;
      this.fadeInAnimate = true;
      setTimeout(() => {
        this.$refs.prepareForm.refreshScroll();
      }, 220);
    },
    getFormData() {
      if (this.indexOfActiveShowList === -1) {
        return {};
      } else {
        if (
          this.activeShowList[this.indexOfActiveShowList].hasOwnProperty(
            "prepare_form"
          )
        ) {
          console.log("form data props:");
          console.log(
            this.activeShowList[this.indexOfActiveShowList].prepare_form
          );
          return this.activeShowList[this.indexOfActiveShowList].prepare_form;
        } else {
          console.log("form data props: empty");
          return {};
        }
      }
    },
    closePrepareForm() {
      this.fadeInAnimate = false;
      setTimeout(() => {
        this.ifShowPrepareForm = false;
      }, 150);
    },
    async confirmOrder(index) {
      const orderid = this.activeShowList[index].id;
      console.log(orderid);
      const response = await $confirmOrder({
        id: orderid,
      });
      console.log("order", response);
      // 改变对应订单的status字段
      if ((response.Code = 200 && response.msg === "success")) {
        //console.log(response.details);
        //const copy = window.JSON.parse(window.JSON.stringify(this.activeShowList[index]));

        //const temp = this.allList.find(item => item.status === 1103);
        /* const tempIndex = this.allList.indexOf(temp);
        console.log(tempIndex); */

        this.$set(this.activeShowList[index], "status", 1103);

        /* const obj = window.JSON.parse(window.JSON.stringify(this.activeShowList[index]));
        this.activeShowList.splice(index, 1);
        const indexInActiveList = (this.currentPage - 0) * this.pageSize + index;
        this.activeList.splice(indexInActiveList, 1); */

        /* const index2 = this.allList.indexOf(this.activeShowList[index]);

        this.allList.splice(index2, 1);
        this.allList.splice(tempIndex - 1, 0, copy); */

        this.activeShowList.splice(index, 1);
      } else {
        this.$toast.error("出错", 240);
      }
    },
    async completeOrder(index) {
      const orderid = this.activeShowList[index].id;
      console.log(orderid);
      const response = await $completeOrder({
        id: orderid,
      });
      console.log(response);
      if (response.Code === 200 && response.msg === "success") {
      } else {
      }
    },
  },
  watch: {
    currentPage(newVal) {
      this.activeShowList = this.activeList.slice(
        this.pageSize * (newVal - 1),
        this.pageSize * newVal
      );
      this.$refs.scroll.scrollTo(0, 0, 200);
    },
    labelCode(newVal, oldVal) {
      if (newVal === oldVal) return;

      if (newVal === 1100) {
        this.activeList = this.allList;
      } else {
        const filters = this.allList.filter((item) => item.status === newVal);
        this.activeList = filters;
      }

      this.activeShowList = this.activeList.slice(0, 8);
      this.currentPage = 1; // 并不会引起watch中currentPage中函数的执行

      this.$refs.scroll.scrollTo(0, 0, 200);
    },
  },
  components: {
    TabBar,
    Scroll,
    ContentList,
    PrepareForm,
    EmptyDataPlaceholder,
    ProductList,
  },
};
</script>

<style scoped>
