  width: 820px;
  /* min-width: 100%; */
  /* display: flex; */
  /* flex-direction: column; */
  margin: 0 auto;
  /* padding-left: 80px; */
  /* padding-top: 20px; */
}
#order-management .scroll-content {
  /* position: absolute; */
  left: 320px;
  right: 80px;
  top: 58px;
  bottom: 0;

  overflow: hidden;
}
#order-management .scroll-content .paginationZone {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 50px;
}

#order-management .right-cover {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 240px;
  right: 0;

  display: flex;
  justify-content: center;
  align-items: center;
}

.FriendUrlCreateWindowFadeIn {
  animation: FadeIn 0.2s linear;
}
.FriendUrlCreateWindowFadeOut {
  animation: FadeOut 0.2s linear;
}

@keyframes FadeIn {
  0% {
    transform: scale(0);
  }
  20% {
    transform: scale(0.2);
  }
  60% {
    transform: scale(0.6);
  }
  80% {
    transform: scale(0.8);
  }
