<script>
import ContentListItem from "./ContentListItem.vue";
import { processTimeToMinInProviderOrderShow } from "../../../../../common/utils";
export default {
  name: "e-ContentList",
  mounted() {
    this.copyBtn = new this.clipboard(".copyIcon");
  },
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      copyBtn: null,
    };
  },
  methods: {
    processCreateTime(time) {
      return processTimeToMinInProviderOrderShow(time);
    },
    toastAfterCopy() {
      const vm = this;
      let clipboard = vm.copyBtn;
      clipboard.on("success", function () {
        vm.$toast.success("复制成功", 240);
      });
      clipboard.on("error", function () {
        vm.$toast.error("复制失败", 240);
      });
    },
    changeControln(index) {
      this.$emit("changeControln", index);
    },
    processRealidPresent(controln, realid) {
      if (controln === 0) {
        return `${realid.substr(0, 6)}********${realid.substr(14, 4)}`;
      }
      return realid;
    },
    checkPrepareFormClick(index) {
      this.$emit("checkFormClick", index);
    },
    confirmOrder(index) {
      this.$emit("confirmOrder", index);
    },
    completeOrder(index) {
      this.$emit("completeOrder", index);
    },
  },
  components: {
    ContentListItem,
  },
};
</script>

